@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;600;700&display=swap);
body {
	margin: 0;
	font-family: "Rubik", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	letter-spacing: 0.1px;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}
input {
	font-family: "Rubik", sans-serif;
}
input:focus-visible {
	outline: none;
}

h2,
h1,
h3,
p,
h4,
span,
button {
	margin: 0;
	padding: 0;
	border: none;
}

.App {
	text-align: center;
	height: 100vh;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		-webkit-animation: App-logo-spin infinite 20s linear;
		        animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

.top-div {
	background-image: url(/static/media/pattern-bg.a60a3c85.png);
	padding: 30px 30px 80px;
	position: relative;
	box-shadow: 1px 4px 6px 2px rgb(189 189 189 / 41%);
	box-sizing: border-box;
	height: 30vh;
}

.top-div .page-main-title {
	color: #fff;
}
.my-map-container {
	height: 65vh;
	overflow: hidden;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}
footer {
	position: fixed;
	width: 100%;
	height: 5vh;
	background-image: url(/static/media/pattern-bg.a60a3c85.png);
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	color: #fff;
}

@media screen and (max-width: 425px) {
	.top-div {
		padding: 30px;
		height: 27vh;
	}
	.my-map-container {
		height: 70vh;
	}
	footer {
		font-size: 13px;
		height: 3vh;
	}
	.top-div .page-main-title {
		font-size: 21px;
	}
}
@media screen and (max-width: 375px) {
	.top-div {
		padding: 25px 20px;
	}
}
@media screen and (max-width: 320px) {
	.top-div {
		padding: 25px 10px;
	}
}

.input-container {
	display: -webkit-inline-flex;
	display: inline-flex;
	background-color: #ffff;
	margin: 40px;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	border-radius: 10px;
}
.input-container input {
	border: none;
	padding: 15px;
	font-size: 16px;
	width: 450px;
	border: 1px solid #fff;
	border-top-left-radius: 10px;
	border-bottom-left-radius: 10px;
	transition: 200ms ease-in-out;
}
.input-container input:focus {
	box-shadow: 1px 4px 6px 2px rgb(0 0 0 / 41%);
	border: 1px solid rgb(0 0 0 / 41%);
	border-right-color: #ffff;
	border-right-width: 0;
	transition: 200ms ease-in-out;
}
.input-container button {
	background-color: hsl(0, 0%, 17%);
	color: #ffff;
	min-height: 100%;
	width: 12%;
	font-size: 16px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	cursor: pointer;
	transition: 500ms ease-in-out;
	border-top-right-radius: 10px;
	border-bottom-right-radius: 10px;
}
.input-container button:hover {
	background-color: hsl(0, 0%, 59%);
	transition: 500ms ease-in-out;
}
.input-container button svg {
	font-size: 18px;
}

@media screen and (max-width: 768px) {
	.input-container input {
		padding: 10px;
	}
}
@media screen and (max-width: 425px) {
	.input-container input {
		width: 88%;
		border-top-left-radius: 5px;
		border-bottom-left-radius: 5px;
	}
	.input-container {
		margin: 20px 0;
		border-radius: 5px;
		width: 100%;
	}
	.input-container button {
		border-top-right-radius: 5px;
		border-bottom-right-radius: 5px;
	}
} ;

/* Commeting */

.card-container {
	position: absolute;
	width: 100%;
	bottom: -100px;
	left: 0;
	min-height: 160px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	z-index: 9999;
}
.card-wrapper {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: center;
	        align-items: center;
	width: 75%;
	height: 100%;
	background-color: #fff;
	border-radius: 15px;
	padding: 20px;
	box-sizing: border-box;
	box-shadow: 1px 4px 6px 2px rgb(189 189 189 / 41%);
}
.card-item {
	text-align: left;
	padding-right: 20px;
}
.card-item h4 {
	color: hsl(0, 0%, 59%);
	margin-bottom: 20px;
	font-size: 15px;
}
.card-item h2 {
	color: hsl(0, 0%, 17%);
	max-width: 250px;
	width: 200px;
	display: -webkit-box;
	-webkit-line-clamp: 3;
	min-height: 85px;
	-webkit-box-orient: vertical;
	overflow: hidden;
}
.card-wrapper .line {
	padding: 1px;
	height: 80px;
	background: hsl(0deg 0% 59% / 41%);
}

@media screen and (max-width: 1024px) {
	.card-wrapper {
		width: 85%;
	}
	.card-wrapper h2 {
		font-size: 21px;
		width: 169px;
		max-width: 169px;
		min-height: 75px;
	}
	.card-item {
		padding: 0;
	}
	.card-wrapper h4 {
		font-size: 14px;
	}
}

@media screen and (max-width: 768px) {
	.card-wrapper {
		padding: 15px 10px;
		width: 90%;
	}
	.card-wrapper h4 {
		font-size: 13px;
	}
	.card-wrapper h2 {
		font-size: 17px;
		max-width: 140px;
		width: 140px;
	}
}
@media screen and (max-width: 425px) {
	.card-container {
		height: unset;
		bottom: -205px;
	}
	.card-wrapper {
		display: block;
		width: 70%;
		padding: 12px;
		border-radius: 10px;
	}
	.card-wrapper .line {
		display: none;
	}
	.card-wrapper h2 {
		min-height: unset;
		max-width: unset;
		width: unset;
		-webkit-line-clamp: 2;
		font-size: 16px;
	}
	.card-item {
		text-align: center;
		margin-bottom: 20px;
	}
	.card-item.last {
		margin-bottom: 0;
	}
	.card-wrapper h4 {
		margin-bottom: 5px;
		font-size: 12px;
	}
}

